<template>
  <div class="box">
    <div class="middle">
      <div class="content">
        <div class="text">
          <div>免费获取</div>
          <div>医生专业心电图解读服务</div>
        </div>
        <div class="phone">
          <img src="../assets/img/phone_icon@2x.png" alt="">
          <span>{{phone}}</span>
        </div>
      </div>
    </div>
    <div class="btn">
      <a :tel="phone">立即呼叫</a>
    </div>
    <div class="code">
      本次解读的检查号：{{code}}
    </div>
  </div>
</template>

<script>
export default {
  name: "phone",
  data(){
    return{
      phone:'400-8888-8888',
      code:'111111111'
    }
  }
}
</script>

<style scoped>
.box{
  width: 100%;
  height: 100%;
  position: relative;
}
.middle{
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.content{
  width: 100%;
}
.content .text{
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 2.5rem;
}
.phone img{
  width: 1.3rem;
  position: relative;
  top: 0.3rem;
}
.phone{
  font-size: 1.2rem;
}
.btn{
  display: flex;
  justify-content: center;
}
.btn a{
  width: 60%;
  padding: 1rem;
  color: #fff;
  background: linear-gradient(left, #1CE5CD 0%,#1BA4B5 100%);
  border-radius: 3rem;
  font-size: 1.3rem;
  font-weight: 600;
}
.code{
  position: absolute;
  left: 0;
  bottom: 5rem;
  width: 100%;
}
</style>



